.bg-cloud {
    background: #f5f6fa;
}

.max-w-1000px {
    max-width: 1000px;
}

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, 300px);
    column-gap: 2rem;   /*min column width*/
    row-gap: 3rem;
    justify-content: space-evenly;
}

.projects-grid .imgContainer {
    height: 200px;
    width:100%;
}

.projects-grid .imgContainer > img{
    height: 100%;
    width:100%;
    object-fit: cover;
}

.list-radio {
    top: .3rem;
    height: 10px;
    width: 10px;
}

.overflow-hidden {
    overflow: hidden;
}

.align-self-center {
    align-self: center;
}