.prompt-w-90 {
    width: 90%;
}

@media (min-width: 576px) {
    .prompt-w-sm-70 {
        width: 70%;
    }
}

@media (min-width: 992px) {

    .prompt-w-lg-40 {
        width: 40%;
    }

    .contact-fl-lg-row {
        flex-direction: row;
    }

    .contact-fl-lg-space-even {
        justify-content: space-evenly;
    }
}

.form-w {
    min-width: 250px;
    width: 90%;
}

@media (min-width: 576px) {
    .form-w {
        width: 450px;
        flex-basis: 450px;
        flex-grow: 0;
        flex-shrink: 0;
    }
}
