.toggle-menu-icon {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    padding: 0;
    outline: none;
}
.toggle-menu-icon .line {
    fill: none;
    stroke: black;
    stroke-width: 6;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
        stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.toggle-menu-icon .line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
}
.toggle-menu-icon .line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6;
}
.toggle-menu-icon .line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
}
.toggle-menu-icon.opened .line1 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
}
.toggle-menu-icon.opened .line2 {
    stroke-dasharray: 1 60;
    stroke-dashoffset: -30;
    stroke-width: 6;
}
.toggle-menu-icon.opened .line3 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
}


@media (min-width: 768px) {

    .toggle-menu-icon {
      display: none;
    }
    
}