.container {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
  }
  
  @keyframes arrows {
    0%,
    100% {
      color: black;
      transform: translateY(0);
    }
    50% {
      color: #3AB493;
      transform: translateY(20px);
    }
  }
  
.container span {
    animation: arrows 1s var(--delay) infinite ease-in;
    font-size: 2rem;
}

.arw1 {
    --delay: 0s;
}

.arw2 {
    --delay: 0.1s
}

.arw3 {
    --delay: 0.3s
}

.arw4 {
    --delay: 0.4s
}

.arw5 {
    --delay: 0.5s
}